import Header from '../components/Header'
import Footer from '../components/Footer'
import { useEffect } from 'react'

export default function Home() {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (window.AOS) window.AOS.init({ duration: 1000 })
      if (window.GLightbox) window.GLightbox()
      if (window.Isotope && document.querySelector('.isotope-container')) {
        new window.Isotope('.isotope-container', {
          itemSelector: '.portfolio-item',
          layoutMode: 'fitRows'
        })
      }
      if (window.initMain) window.initMain()
    }
  }, [])

  return (
    <>
      <Header />
      <main>
        {/* Insert full static content here (copied from your HTML), IDs must match JS expectations */}
      </main>
      <Footer />
    </>
  )
}